/* App.css */

/* Reset default browser styles */
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
font,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  font-size: 100%;
  vertical-align: baseline;
  background: transparent;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

body {
  line-height: 1;
}

ol,
ul {
  list-style: none;
}

blockquote,
q {
  quotes: none;
}

/* Global styles */
body {
  font-family: 'Raleway', 'Helvetica Neue', Helvetica, Arial, sans-serif;
  font-size: 16px;
  line-height: 1.6;
  font-weight: 300;
}

/* Header styles */
video {
  width: 100%;
  height: auto;
  object-fit: cover;
}

.page-header {
  position: relative;
  overflow: hidden;
} 

.intro {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
  z-index: 2;
  color: #fff;
}

.intro .project-name {
  font-size: 36px;
  font-weight: 400;
  margin-bottom: 10px;
}

.intro .highlight {
  font-size: 48px;
  font-weight: 700;
  margin-bottom: 20px;
  color: #89CFEF;
}

.intro .project-tagline {
  font-size: 24px;
  font-weight: 300;
  margin-bottom: 20px;
}

.intro .btn {
  display: inline-block;
  padding: 12px 24px;
  font-size: 18px;
  font-weight: 500;
  text-decoration: none;
  background-color: #1C2951;
  color: #fff;
  border-radius: 4px;
  transition: background-color 0.3s ease;
}

.intro .btn:hover {
  background-color: #F4BCC3;
}

/* Navigation styles */
nav {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.6);
  z-index: 1000;
  transition: top 0.3s ease;
}

nav.sticky {
  top: 0;
}

nav ul {
  display: flex;
  justify-content: center;
  list-style-type: none;
  flex-direction: row;
}

nav ul li {
  margin: 15px 15px;
}

nav ul li a {
  text-decoration: none;
  color: #fff;
  font-size: 18px;
  font-weight: 300;
  transition: color 0.3s ease;
  list-style-type: none;
}

nav ul li a:hover {
  color: #F4BCC3;
}

/* Main content styles */
main {
  padding: 100px 0;
  background-color: #fff;
  color: #333;
}

.container {
  margin: 0 auto;
  max-width: 800px;
  padding: 0 20px;
}

h2 {
  font-size: 36px;
  font-weight: 700;
  margin-bottom: 20px;
  justify-content: center;
}

p {
  font-size: 18px;
  font-weight: 300;
  margin-bottom: 20px;
}

ul li {
  font-size: 18px;
  font-weight: 300;
  margin-bottom: 10px;
}

ul li::before {
  margin-right: 5px;
}

a {
  color: #007bff;
  text-decoration: none;
  font-weight: 400;
  transition: color 0.3s ease;
}

a:hover {
  color: #0056b3;
}

/* Barchart styles */
.barchart {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 400px;
  height: 300px;
}

/* Footer styles */
.site-footer {
  background-color: #1C2951;
  text-align: center;
  color: #F4BCC3;
  width: 100%;
}

.site-footer h2 {
  font-size: 36px;
  font-weight: 700;
  margin-bottom: 20px;
  color: white;
}

.site-footer p {
  font-size: 18px;
  font-weight: 300;
  margin-bottom: 20px;
}

.site-footer a {
  color: white;
  text-decoration: none;
  font-weight: 400;
  transition: color 0.3s ease;
}

.site-footer a:hover {
  color: #0056b3;
}

.site-footer-social {
  flex-direction: row;
  gap: 20px;
  margin-top: 10px;
}
.site-footer-owner {
  font-size: 14px;
  color: #aaa;
}

.site-footer-owner a {
  color: #aaa;
}

/* Media Queries */
@media screen and (max-width: 600px) {
  .container {
    margin: 10px;
    max-width: none;
    padding: 0;
  }
}